<script>
	import './main.scss';

	import router, { curRoute } from './router.js';
	import RouterLink from './RouterLink.svelte';
	import { cart, user } from './stores.js';
	import Sidebar from './sidebar.svelte';
	import LogInComponent from './logInComponent.svelte';

	let sidebar_show = false;

  //const unsibscribe = authState(auth).subscribe( u => user => u );

	function moveToCartView(){
		curRoute.set('/cart');
		window.history.pushState({path: '/cart'}, '', window.location.origin + '/cart');
	}

	function moveToEmployeeView(){
		curRoute.set('/employee');
		window.history.pushState({path: '/employee'}, '', window.location.origin + '/employee');
	}

	function moveToLandingView(){
		curRoute.set('/');
		window.history.pushState({path: '/'}, '', window.location.origin + '/');
	}

	function togNav(){
		document.getElementById("navbarDustin").classList.contains("is-active") ?
		document.getElementById("navbarDustin").classList.remove("is-active") :
		document.getElementById("navbarDustin").classList.add("is-active")
	}

	function toggleDropDown(){
		document.getElementById("dropDown").classList.contains("is-active") ?
		document.getElementById("dropDown").classList.remove("is-active") :
		document.getElementById("dropDown").classList.add("is-active")
	}

	function resetInventory(){
		var orderRef = db.collection("inventory")
		.get()
		.then(function(querySnapshot) {
			querySnapshot.forEach(function(doc) {
				db.collection("inventory").doc(doc.id).update({
					status: "available"
				})
				.then(function() {
						console.log("Document successfully updated!");
				})
				.catch(function(error) {
						console.error("Error updating document: ", error);
				});
			});
		})
	}


</script>

<main>

  <nav class="navbar is-dark " role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
			<div class="navbar-item">
				<button class="button is-info" on:click={moveToLandingView}> <strong> HOME </strong> </button>
			</div>
			<a role="button" class="navbar-burger" aria-label="menu" aria-expanded="false" data-target="navbarDustin"on:click={togNav}>
			  <span aria-hidden="true"></span>
			  <span aria-hidden="true"></span>
			  <span aria-hidden="true"></span>
			</a>
    </div>
		<div id="navbarDustin" class="navbar-menu">
		 	<div class="navbar-end">
				{ #if $cart.length > 0 }
					<a class="navbar-item" on:click={moveToCartView} style="text-decoration: none;">
						<i class="fas fa-2x fa-shopping-cart"></i>
						<strong style="color:inherit;"> { $cart.length } </strong>
					</a>
				{/if}
				<a class="navbar-item" on:click={moveToEmployeeView} style="text-decoration: none;">
					<i class="fas fa-2x fa-eye"></i>
				</a>
				<div id="dropDown" class="navbar-item has-dropdown" >
					<div class="navbar-link is-arrowless" on:click={toggleDropDown}>
						<i class="is-right fas fa-2x fa-user-circle" ></i>
					</div>

					<div class="navbar-dropdown is-right" style="min-width: 24rem; padding: 1rem;">
	          <div class="" style="width:100% ">
	            <LogInComponent/>
	          </div>
					</div>


				</div>
			</div>
		</div>
  </nav>

	<!-- <Sidebar bind:show={sidebar_show} /> -->

</main>

<style>
	.is-right{
		left: 50;
	}
