<script>
import './main.scss';
import { cart } from './stores.js';
import { onMount } from 'svelte';

export let tool = {
  displayName: "TOOL NAME",
  pricePerHour: 5,
  imageName: "powerWasher.jpg"
}

$: selected = $cart.filter(e => e.displayName === tool.displayName).length > 0;


function handleAdd(){
  cart.addItem(tool);
}

function handleRemove(){
  cart.removeItem(tool);
}

onMount(async () => {

});


</script>

<style>
  .priceTitle {
    padding-top: 5px;
  }
</style>

<div class="box">
  <h3 class="title is-3"> {tool.displayName} </h3>
  <figure class="image is-square is-100x100">
    <img src={tool.imageName} alt="">
  </figure>

  <h4 class="title is-4 priceTitle"> ${tool.costPerHour}/hour </h4>

  <div>
    { #if selected }
      <button class="button is-info is-fullwidth" on:click={handleRemove}><strong>REMOVE FROM CART</strong></button>
    { :else }
      <button class="button is-primary is-fullwidth" on:click={handleAdd}><strong>ADD TO CART</strong></button>
    { /if }

  </div>


</div>
