import {
	writable
} from 'svelte/store'

/*
  CART
*/
function removeItem(array, item) {
	let forDeletion = [item];
	let newArray = array.filter(item => !forDeletion.includes(item))
	return newArray;
}

function createCart() {
	const {
		subscribe,
		set,
		update
	} = writable([]);

	return {
		subscribe,
		addItem: (newItem) => update(n => [...n, newItem]),
		removeItem: (newItem) => update(n => removeItem(n, newItem)),
		reset: () => set([])
	};
}

export const cart = createCart();

