<script>

import './main.scss';
import { user } from './stores.js';
import { db, auth, googleProvider } from './firebase.js';
import { mapsApiKey } from './api.js';
import Map from './Map.svelte'
import { collectionData } from 'rxfire/firestore';
import { startWith } from 'rxjs/operators';


let expanded = false;
$: userLatitude = 0;
$: userLongitude = 0;

export let order = {name: 'TEZT'};
export let isEmployeeView = false;
export let inventoryData = [];
export let toolData = [];

let date = order.time_stamp.split("T")[0];
let time = order.time_stamp.split("T")[1].split(".")[0].replace("Z", "");

$: statusColor = () => {
  switch (order.status){
    case "createdz":
      return "#0000AA";

    default:
      return "#222222";
  }
}

function getToolDataForTbId(id){
  const tool = $inventoryData.find((el) => { return el.tb_id === id });
  const toolData = $toolData.find( (element) => { return element.name === tool.data });
  return toolData;
}

function toggleExpanded(){
  expanded = !expanded;
}
function latLonCheck(){
  if (order.status!=='delivering'){
    return false
  }
  let storageLatitude=41.511171;
  let storageLongitude=-112.031399;
  let distance=Math.pow(Math.pow(userLatitude-storageLatitude, 2)+Math.pow(userLongitude-storageLongitude, 2), 1/2);
  if (distance > .0097) {
    return true
  }
  return false
}
function processOrderAcceptance(){
  var orderRef = db.collection("orders").where("order_id", "==", order.order_id)
  .get()
  .then(function(querySnapshot) {
    querySnapshot.forEach(function(doc) {
      db.collection("orders").doc(doc.id).update({
        employee: $user.userId,
        status: "processing"
      })
      .then(function() {
          console.log("Document successfully updated!");
          order.employee=$user.userId,
          order.status = "processing"
      })
      .catch(function(error) {
          // The document probably doesn't exist.
          console.error("Error updating document: ", error);
      });
    });
  })
}

function processOrderCheckout(){
  var orderRef = db.collection("orders").where("order_id", "==", order.order_id)
  .get()
  .then(function(querySnapshot) {
    querySnapshot.forEach(function(doc) {
      db.collection("orders").doc(doc.id).update({
        status: "delivering"
      })
      .then(function() {
          console.log("Document successfully updated!");
          order.status = "delivering"
      })
      .catch(function(error) {
          // The document probably doesn't exist.
          console.error("Error updating document: ", error);
      });
    });
  })
}

function getLocation() {
  if (navigator.geolocation) {
    navigator.geolocation.watchPosition(showPosition);
  } else {
    alert("GEOLOCATION ERROR");
  }
}

function showPosition(position) {
  const latLonCheckVal = latLonCheck();
  if(latLonCheckVal && isEmployeeView){
    userLatitude = position.coords.latitude;
    userLongitude = position.coords.longitude;

    var orderRef = db.collection("orders").where("order_id", "==", order.order_id)
    .get()
    .then(function(querySnapshot) {
      querySnapshot.forEach(function(doc) {
        db.collection("orders").doc(doc.id).update({
          delivery_latitude: userLatitude,
          delivery_longitude: userLongitude
        })
        // .then(function() {
        //     console.log("Document successfully updated!");
        // })
        // .catch(function(error) {
        //     console.error("Error updating document: ", error);
        // });
      });
    })
  }
  else{
    userLatitude = order.delivery_latitude;
    userLongitude = order.delivery_longitude;
  }
}

function processOrderDelivery(){
  var orderRef = db.collection("orders").where("order_id", "==", order.order_id)
  .get()
  .then(function(querySnapshot) {
    querySnapshot.forEach(function(doc) {
      db.collection("orders").doc(doc.id).update({
        status: "at customer",
        start_time: Date.now()
      })
      .then(() => {
        order.status = "at customer";
        console.log("updated");
      })
    });
  })
}

function processPickupRequest(){
  var orderRef = db.collection("orders").where("order_id", "==", order.order_id)
  .get()
  .then(function(querySnapshot) {
    querySnapshot.forEach(function(doc) {
      db.collection("orders").doc(doc.id).update({
        status: "pickup requested",
        finish_time: Date.now()
      })
      .then(() => {
        order.status = "pickup requested"
        console.log("updated");
      })
    });
  })
}

function processOrderCompletion(){
  var orderRef = db.collection("orders").where("order_id", "==", order.order_id)
  .get()
  .then(function(querySnapshot) {
    querySnapshot.forEach(function(doc) {
      db.collection("orders").doc(doc.id).update({
        status: "completed",
      })
      .then(() => {
        order.status = "completed"
        console.log("updated");
      })
    });
  })
}

let statusColors = {
  "created": "#fffa65",
  "processing": "#3ae374",
  "delivering": "#3ae374",
  "at customer": "#3ae374",
  "pick-up requested": "#fffa65",
  "pick-up en route": "#3ae374",
  "completed": "#0c2461"
}

if(order.status === "delivering" || order.status ==="pickup requested"){
  setInterval(getLocation, 1000);
}

let possibleStatuses = ["created", "processing", "delivering", "pick-up requested", "pick-up en route", "completed"];
$: statusPercent = ((possibleStatuses.indexOf(order.status) % 3 + 1) * 33 + 1);

</script>


<div class={!expanded ? "is-4 column has-background-light" : "is-6 column has-background-light"} >
  <div class="card">
    <div class="card-image" style={expanded ? "border-bottom-left-radius: 0px; border-bottom-right-radius: 0px;" : "border-bottom-left-radius: 5px; border-bottom-right-radius: 5px;"} on:click={toggleExpanded}>
      <h3 class="title is-3" style="margin-top:5px;">
        {order.name}
      </h3>
      <h3 class="subtitle">
        {date} <br/>
        {time}
      </h3>
      <h3 class="subtitle">
        Status: { order.status }
      </h3>
      <div class="section">
        <progress class="progress is-primary" value={statusPercent} max="100"> </progress>
      </div>
    </div>

    {#if expanded === true}
      <div class="content">
        <div class="card-content">
          <div class="columns is-centered is-vcentered">
            <div class="colmun is-8">
              <div>
                {order.address}<br/>{order.city},{order.state},{order.zip_code}
              </div>
            </div>
            <div class="column is-2">
              <a class="button is-success is-medium" href="https://maps.google.com/?q={order.address} {order.city},{order.state},{order.zip_code}">
                <span class="icon is-medium">
                  <i class="fas fa-map-marker-alt"></i>
                </span>
              </a>
            </div>
          </div>

          {#if isEmployeeView === true}

            <div class="box">
              <h3 class="subtitle"> Tools </h3>
              <div class="columns is-multiline is-centered">
                {#each order.tools as tool}
                  <div class="column is-7">
                    <label class="checkbox is-pulled-left">
                      <input type="checkbox">
                      {tool}
                    </label>
                  </div>
                {/each}
              </div>
              {#if order.status === "processing"}
                <button class="button is-primary is-fullwidth is-large" on:click={processOrderCheckout}>
                  <strong>CHECK OUT</strong>
                </button>
              {/if}
            </div>
            {#if order.status === "created"}
              <button class="button is-primary is-fullwidth is-large" on:click={processOrderAcceptance}>
                <strong>ACCEPT</strong>
              </button>
            {/if}
            {#if order.status === "delivering"}
              <button class="button is-primary is-fullwidth is-large" on:click={processOrderDelivery}>
                <strong>DELIVERED</strong>
              </button>
            {/if}
            {#if order.status === "pickup requested"}
              <button class="button is-primary is-fullwidth is-large" on:click={processOrderCompletion}>
                <strong>FINSH ORDER</strong>
              </button>
            {/if}

          {:else}
            <div class="box">
              <h3 class="subtitle"> Tools </h3>
              <div class="columns is-multiline is-centered">
                {#each order.tools as tool}
                  <div class="column is-7">
                    <strong class="is-pulled-left">
                      {getToolDataForTbId(tool).displayName}
                      <!-- {toolData[tool.data].displayName} -->
                    </strong>
                  </div>
                {/each}
              </div>
            </div>
            {#if order.status==="delivering"}
              <div style="padding-top: 10px;">
                <Map lat={userLatitude} long={userLongitude}/>
              </div>
            {/if}
            {#if order.status==="at customer"}
            <button class="button is-primary is-fullwidth is-large" on:click={processPickupRequest}>
              <strong>REQUEST PICKUP</strong>
            </button>
            {/if}
          {/if}



        </div>
      </div>

    {/if}


  </div>
</div>

<style>
.card {
  border-radius: 5px;
}

.card-image{
  cursor: pointer;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  background-color: #cccccc;
}
a{
  text-decoration: none;
}

</style>
