<script>
	import './main.scss';

	import ToolDisplay from './toolDisplay.svelte';
  import Navbar from './navbar.svelte';
	import { db } from './firebase.js';
	import { cart, user } from './stores.js';
	import { collectionData } from 'rxfire/firestore';
	import { startWith } from 'rxjs/operators';
	import LogInComponent from './LoginComponent.svelte';

	var nextButtonDisabled = false;
	var orderName = "";
	var orderAddress = "";
	var orderCity = "";
	var orderZip = "";
	var orderState = "";
	var ordertools = []
	var checkoutPageNumber = 0;

	const inventoryQuery = db.collection('inventory'); //.orderBy('displayName');
	var inventory = collectionData(inventoryQuery).pipe(startWith([]));

	function toggleModal(){
		document.getElementById("purchaseModal").classList.add("is-active");
	}

	function hideModal(){
		document.getElementById("purchaseModal").classList.remove("is-active");
	}

  $: costs = $cart.map( x => x.costPerHour).reduce(function(a, b){
        return a + b;
    }, 0);

  $: totalCosts = $cart.map( x => x.initialCost).reduce(function(a, b){
        return a + b;
    }, 0);

		function createOrder(){
			let orderUser = $user.userId;

			let toolIDs = []
			for(var i=0; i<$cart.length; i++){

				let potentialMatches = [];
				for (var j=0; j<$inventory.length; j++){

					if ($inventory[j].data === $cart[i].name && $inventory[j].status === "available"){
						potentialMatches.push($inventory[j]);
					}
				}

				let match = potentialMatches[0];
				if (potentialMatches.length > 1){
					let index = Math.floor(Math.random() * potentialMatches.length);
					match = potentialMatches[index];
				}
				toolIDs.push(match.tb_id);

				var orderRef = db.collection("inventory").where("tb_id", "==", match.tb_id)
				.get()
				.then(function(querySnapshot) {
					querySnapshot.forEach(function(doc) {
						db.collection("inventory").doc(doc.id).update({
							status: "reserved"
						})
						.then(function() {
								console.log("Document successfully updated!");
						})
						.catch(function(error) {
								console.error("Error updating document: ", error);
						});
					});
				})
			}

			debugger;
			let newDate = new Date(Date.now()).toISOString();
			let newDateIso = newDate.split("T")[0].replace(/-/g, "");
			let newKey = Math.ceil(Math.random() * 10000) + newDateIso;

			var docData = {
				order_id: newKey,
		    user: orderUser,
		    address: orderAddress,
				city: orderCity,
				zip_code: orderZip,
				state: orderState,
		    name: orderName,
				tools: toolIDs,
				start_time: null,
				finish_time: null,
				employee: null,
				delivery_latitude: 0,
				delivery_longitude: 0,
				status: "created",
		    time_stamp: new Date(Date.now()).toISOString()
			};

			var newDoc = db.collection("orders").doc();

			newDoc.set(docData).then(function() {
		    alert("Document successfully written!");
				hideModal();
		  });

		}

		function modalNextButtonPress(){
			checkoutPageNumber++;
			modalTextCheck();
		}

		function modalBackButtonPress(){
			checkoutPageNumber--;
			modalTextCheck();
		}

		function modalTextCheck(){

			switch (checkoutPageNumber){
				case 0:
					nextButtonDisabled = false;
					break;

				case 1:
					if(orderAddress !== "" &&
							orderCity !== "" &&
							orderZip !== "" &&
							orderState !== ""){
								nextButtonDisabled = false;
							}
					else{
						nextButtonDisabled = true;
					}
				break;

				case 2:
					if(orderName !== ""){
						nextButtonDisabled = false;
					}
					else{
						nextButtonDisabled = true;
					}
				break;

				default:
				break;

			}
		}

</script>

<main>
  <Navbar/>

	<section class="section has-background-light">
		<h1 class='title'>MY CART</h1>
      { #if $cart.length > 0 }
        <div class="columns is-multiline">
  			  {#each $cart as product}
            <div class="column is-one-quarter">
              <ToolDisplay tool={product} />
            </div>
  			  {/each}
        </div>
      {/if}

    </section>
    <section class="section has-background-white">

      <nav class="panel is-dark">
        <p class="panel-heading">
          ORDER DETAILS
        </p>

        <div class="panel-block columns is-mobile is-centered">

          <div class="column is-half">
            <h1 class="is-subtitle"> <strong> NAME </strong> </h1>
          </div>
          <div class="column is-one-quarter">
            <h1 class="is-subtitle is-pulled-right"> <strong> INITIAL  </strong> </h1>
          </div>
          <div class="column is-one-quarter">
            <h1 class="is-subtitle is-pulled-right"> <strong> HOURLY </strong> </h1>
          </div>


        </div>

        {#each $cart as product}
          <div class="panel-block columns is-mobile is-centered">

            <div class="column is-half">
              <h1 class="is-subtitle"> {product.displayName} </h1>
            </div>
            <div class="column is-one-quarter">
              <h1 class="is-subtitle is-pulled-right"> <strong> ${product.initialCost} </strong> </h1>
            </div>
            <div class="column is-one-quarter">
              <h1 class="is-subtitle is-pulled-right"> ${product.costPerHour}/hour </h1>
            </div>

          </div>
        {/each}

        <div class="panel-block columns is-mobile is-centered is-light">

          <div class="column is-half">
            <h1 class="is-subtitle"> <strong> TOTAL </strong> </h1>
          </div>
          <div class="column is-one-quarter">
            <h1 class="is-subtitle is-pulled-right"> <strong> ${totalCosts} </strong> </h1>
          </div>
          <div class="column is-one-quarter">
            <h1 class="is-subtitle is-pulled-right"> ${costs}/hour </h1>
          </div>

        </div>

        <div class="panel-block is-pulled-right">
					<div class="is-pulled-right is-full-width">
          	<button class="button is-info is-medium " on:click={toggleModal}><strong>PLACE ORDER</strong></button>
					</div>
        </div>
      </nav>

	</section>


	<div id="purchaseModal" class="modal">
	<div class="modal-background"></div>
	<div class="modal-card">
		<header class="modal-card-head">
			<p class="modal-card-title">Confirm Purchase </p>
			<button on:click={hideModal} class="delete" aria-label="close"></button>

		</header>
		<section class="modal-card-body is-clipped">
		<div>
			<progress class="progress is-primary" value={checkoutPageNumber * 25} max="100">15%</progress>
		</div>
		{#if checkoutPageNumber == 0}
			<h2 class="title is-2">
				Do you want to log in?
			</h2>
			<h2 class="subtitle">
				It's not required, but it is nice for record keeping
			</h2>
			<LogInComponent />
		{:else if checkoutPageNumber ==1}
			<h2 class="title is-2">
				Delivery Info
			</h2>
			<!-- <div id="addressBox" class="field border-dark"> -->
			<div class="control columns is-multiline">
				<div class="column is-12">
					<label class="label is-pulled-left">Address</label>
					<input class="input" type="" placeholder="" bind:value={orderAddress} on:keyup={modalTextCheck}>
				</div>
				<div class="column is-6">
					<label class="label">City</label>
					<input class="input" type="" placeholder="" bind:value={orderCity} on:keyup={modalTextCheck}>
				</div>
				<div class="column is-4">
					<label class="label">State</label>
					<input class="input" type="" placeholder="" bind:value={orderState} on:keyup={modalTextCheck}>
				</div>
				<div class="column is-2">
					<label class="label">ZIP Code</label>
					<input class="input" type="" placeholder="" bind:value={orderZip} on:keyup={modalTextCheck}>
				</div>
			</div>
			<!-- </div> -->
			{:else if checkoutPageNumber == 2}
			<h2 class="title is-2">
				What is your name?
			</h2>
			<div class="field">
				<label class="label is-pulled-left">Name</label>
				<div class="control">
					<input class="input" type="" placeholder="" bind:value={orderName} on:keyup={modalTextCheck}>
				</div>
			</div>
			{:else if checkoutPageNumber == 3}
			<h2 class="title is-2" >
				Payment Info
			</h2>
			<div class="field" >
				<label class="label">Credit Card - TODO</label>
				<div class="control">
					<input class="input" type="text" placeholder="e.g. alexsmith@gmail.com">
				</div>
			</div>
			{/if}
		</section>
		<footer class="modal-card-foot is-pulled-right">
			{#if checkoutPageNumber > 0}
				<button class="button is-success" on:click={modalBackButtonPress}><strong><i class="fas fa-arrow-left"></i> BACK </strong></button>
			{/if}
			{#if checkoutPageNumber < 3}
				<button disabled={nextButtonDisabled} class="button is-success" on:click={modalNextButtonPress}><strong>NEXT <i class="fas fa-arrow-right"></i></strong></button>
			{/if}
			{#if checkoutPageNumber == 3}
			<div class="is-pulled-right is-fullwidth">
				<button class="button is-success" on:click={createOrder}><strong>PURCHASE</strong></button>
			</div>
			{/if}
		</footer>
	</div>
	</div>

</main>

<style>
  .list-item {
    padding: 1em;
    justify-content: left;
  }
</style>
