<script>
  import { user } from './stores.js'
  import { db, auth, googleProvider } from './firebase.js';
  import { authState } from "rxfire/auth";
  const unsibscribe = authState(auth).subscribe( u => user => u );

  let userVal = "";
  let userPasswordVal = "";




	function signUp(){
			auth.createUserWithEmailAndPassword(userVal, userPasswordVal)
			.then(function(e){
        var newUser = {userName: userVal, userId: e.user.uid};
				user.update(u => newUser);
			})
			.catch(function(error) {
			  // Handle Errors here.
			  var errorCode = error.code;
			  var errorMessage = error.message;

				if (errorCode == "auth/email-already-in-use"){
						alert("This email already exists in the system");
				}
				else{
					alert(errorMessage);
				}

			  // ...
			});
			// user.loggedIn = true;
		}


		function logIn() {
			auth.signInWithEmailAndPassword(userVal, userPasswordVal)
			.then(function(e){
        var orderRef = db.collection("employees").where("user_id", "==", e.user.uid)
        .get()

        .then(function(querySnapshot) {
          // if (querySnapshot.docs.length == 1){
          //   debugger;
          // }
          // if (querySnapshot.docs.length == 0){
          //   debugger;
          // }
          // if (querySnapshot.docs.length > 1){
          //   debugger;
          // }

          if (querySnapshot.docs.length == 1){
            querySnapshot.forEach(function(doc) {
              var newUser = {userName: userVal, userId: e.user.uid, isEmployee:true};
              user.update(u => newUser);
            })
          }
          else{
            var newUser = {userName: userVal, userId: e.user.uid, isEmployee:false};
            user.update(u => newUser);
          }


        })
			})
			.catch(function(error) {
				// Handle Errors here.
				var errorCode = error.code;
				var errorMessage = error.message;
				console.log(errorMessage);

				// ...
			});
			// user.loggedIn = true;
		}

    function logOut(){
      let newUser = {userName:"", userId:""}
      user.update(u => newUser);
    }

</script>


<main>
  { #if $user.userId === "" }
    <h2 class="title is-4">
      LOG IN
    </h2>
    <div class="field">
      <p class="control has-icons-left has-icons-right">
        <input class="input" type="email" placeholder="Email" bind:value={userVal}>
        <span class="icon is-small is-left">
          <i class="fas fa-envelope"></i>
        </span>
        <span class="icon is-small is-right">
          <i class="fas fa-check"></i>
        </span>
      </p>
    </div>
    <div class="field">
      <p class="control has-icons-left">
        <input class="input" type="password" placeholder="Password" bind:value={userPasswordVal}>
        <span class="icon is-small is-left">
          <i class="fas fa-lock"></i>
        </span>
      </p>
    </div>
    <div class="field">
      <p class="control">
        <button class="button is-success is-outlined" on:click={signUp}>
          Sign Up
        </button>
        <button class="button is-success is-right" on:click={logIn}>
          Log In
        </button>
      </p>
    </div>
  { :else }
    <h1 class="title is-4 is-dark">LOGGED IN AS {$user.userName}</h1>
    <div class="field">
      <p class="control">
        <button class="button is-success" on:click={logOut}>
          Log Out
        </button>
      </p>
    </div>
  { /if }

</main>


<style>
main {
  width: 100%;
}

</style>
